<template>
  <div>
      <div style="margin-bottom: 20px;">
        <p>大标签 默认样式</p>
        <cds-tag color="default" size="large">default</cds-tag>
        <cds-tag color="blue" size="large">blue</cds-tag>
        <cds-tag color="green" size="large">green</cds-tag>
        <cds-tag color="orange" size="large">orange</cds-tag>
        <cds-tag color="red" size="large">red</cds-tag>
        <br><br>

        <p>中标签 深色样式</p>
        <cds-tag color="default" size="medium" theme="dark">default</cds-tag>
        <cds-tag color="blue" size="medium" theme="dark">blue</cds-tag>
        <cds-tag color="green" size="medium" theme="dark">green</cds-tag>
        <cds-tag color="orange" size="medium" theme="dark">orange</cds-tag>
        <cds-tag color="red" size="medium" theme="dark">red</cds-tag>
        <br><br>

        <p>小标签 描边样式</p>
        <cds-tag color="default" size="mini" theme="plain">default</cds-tag>
        <cds-tag color="blue" size="mini" theme="plain">blue</cds-tag>
        <cds-tag color="green" size="mini" theme="plain">green</cds-tag>
        <cds-tag color="orange" size="mini" theme="plain">orange</cds-tag>
        <cds-tag color="red" size="mini" theme="plain">red</cds-tag>
        <br><br>

        <p>动态添加图标</p>
        <cds-tag v-for="(item,index) in dynamicTags" :key="index" :color="item" closable @close="handleClose(item)">{{item}}</cds-tag>
        <cds-input
          class="input-new-tag"
          v-if="inputVisible"
          v-model="inputValue"
          ref="saveTagInput"
          size="mini"
          @keyup.enter.native="handleInputConfirm"
          @blur="handleInputConfirm"
        >
        </cds-input>
        <cds-button v-else class="button-new-tag" size="mini" @click="showInput" ghost>+ New Tag</cds-button>
      </div>

      <div v-highlight>
        <pre><code>
          &lt;template&gt;
            &lt;div&gt;
              &lt;p&gt;大标签 默认样式&lt;/p&gt;
              &lt;cds-tag color="default" size="large"&gt;default&lt;/cds-tag&gt;
              &lt;cds-tag color="blue" size="large"&gt;blue&lt;/cds-tag&gt;
              &lt;cds-tag color="green" size="large"&gt;green&lt;/cds-tag&gt;
              &lt;cds-tag color="orange" size="large"&gt;orange&lt;/cds-tag&gt;
              &lt;cds-tag color="red" size="large"&gt;red&lt;/cds-tag&gt;

              &lt;p&gt;中标签 深色样式&lt;/p&gt;
              &lt;cds-tag color="default" size="medium" theme="dark"&gt;default&lt;/cds-tag&gt;
              &lt;cds-tag color="blue" size="medium" theme="dark"&gt;blue&lt;/cds-tag&gt;
              &lt;cds-tag color="green" size="medium" theme="dark"&gt;green&lt;/cds-tag&gt;
              &lt;cds-tag color="orange" size="medium" theme="dark"&gt;orange&lt;/cds-tag&gt;
              &lt;cds-tag color="red" size="medium" theme="dark"&gt;red&lt;/cds-tag&gt;

              &lt;p&gt;小标签 描边样式&lt;/p&gt;
              &lt;cds-tag color="default" size="mini" theme="plain"&gt;default&lt;/cds-tag&gt;
              &lt;cds-tag color="blue" size="mini" theme="plain"&gt;blue&lt;/cds-tag&gt;
              &lt;cds-tag color="green" size="mini" theme="plain"&gt;green&lt;/cds-tag&gt;
              &lt;cds-tag color="orange" size="mini" theme="plain"&gt;orange&lt;/cds-tag&gt;
              &lt;cds-tag color="red" size="mini" theme="plain"&gt;red&lt;/cds-tag&gt;

              &lt;p&gt;动态添加图标&lt;/p&gt;
              &lt;cds-tag v-for="(item,index) in dynamicTags" :key="index" :color="item" closable @close="handleClose(item)"&gt;{{item}}&lt;/cds-tag&gt;
              &lt;cds-input
                class="input-new-tag"
                v-if="inputVisible"
                v-model="inputValue"
                ref="saveTagInput"
                size="mini"
                @keyup.enter.native="handleInputConfirm"
                @blur="handleInputConfirm"
              &gt;
              &lt;/cds-input&gt;
              &lt;cds-button v-else class="button-new-tag" size="mini" @click="showInput" ghost&gt;+ New Tag&lt;/cds-button&gt;
            &lt;/div&gt;
          &lt;/template&gt;

          &lt;script&gt;
            export default {
              data() {
                return {
                  dynamicTags:["default","blue","green","orange","red"],
                  inputVisible: false,
                  inputValue: ''
                }
              },
              methods: {
                handleClose(tag) {
                  this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
                },
                showInput() {
                  this.inputVisible = true;
                  this.$nextTick(_ =&gt; {
                    this.$refs.saveTagInput.$refs.input.focus();
                  });
                },
                handleInputConfirm() {
                  let inputValue = this.inputValue;
                  if (inputValue) {
                    this.dynamicTags.push(inputValue);
                  }
                  this.inputVisible = false;
                  this.inputValue = '';
                },
              }
            }
          &lt;/script&gt;

          &lt;style&gt;
            .inline-form {
              width: 100%;
              padding-left: 57px;
            }
            .button-new-tag {
              margin-left: 10px;
              height: 26px;
              padding-top: 0;
              padding-bottom: 0;
            }
            .input-new-tag {
              width: 90px;
              margin-left: 10px;
            }
          &lt;/style&gt;
        </code></pre>
      </div>

      <p>cds-tag Attr</p>
      <cds-table :data="tagData">
        <cds-table-column prop="attr" label="属性"></cds-table-column>
        <cds-table-column prop="attrDescription" label="属性描述" width="300"></cds-table-column>
        <cds-table-column prop="attrType" label="类型"></cds-table-column>
        <cds-table-column prop="attrOptions" label="可选值" width="300"></cds-table-column>
        <cds-table-column prop="attrDefault" label="默认值"></cds-table-column>
      </cds-table>

      <p>cds-tag Event</p>
      <cds-table :data="tagEvent">
        <cds-table-column prop="name" label="事件名称"></cds-table-column>
        <cds-table-column prop="description" label="事件说明" width="600"></cds-table-column>
        <cds-table-column prop="parameter" label="回调参数"></cds-table-column>
      </cds-table>
    </div>
</template>

<script>

  export default {
    name: 'Form',
    data() {
      return {
        dynamicTags:["default","blue","green","orange","red"],
        inputVisible: false,
        inputValue: '',
        tagData: [{
          attr: 'color',
          attrDescription: 'tag标签的颜色样式',
          attrType: 'String',
          attrOptions: 'default | blue | green | orange | red',
          attrDefault: 'default'
        },{
          attr: 'size',
          attrDescription: '大小',
          attrType: 'String',
          attrOptions: 'large | medium | mini',
          attrDefault: ''
        },{
          attr: 'theme',
          attrDescription: '标签主题',
          attrType: 'String',
          attrOptions: 'dark | plain | light',
          attrDefault: 'light'
        },{
          attr: 'closable',
          attrDescription: '是否为可关闭标签',
          attrType: 'Boolean',
          attrOptions: '----',
          attrDefault: 'false'
        }],
        tagEvent: [{
          name: 'click',
          description: '在关闭标签时触发',
          parameter: '(event: Event)'
        },{
          name: 'close',
          description: '在关闭标签时触发',
          parameter: '(event: Event)'
        }],
      }
    },
    methods: {
      handleClose(tag) {
        this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
      },
      showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },
      handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {
          this.dynamicTags.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = '';
      },
    }
  }
</script>

<style>
  .inline-form {
    width: 100%;
    padding-left: 57px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 26px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
  }
</style>